export default class AreaMap {
    constructor(coordinates, myMap, polygonColor) {
        this.coordinates = coordinates;
        this.myMap = myMap;
        this.polygonColor = polygonColor;
        this.geos = []
        this.prepareMap();
    }

    hide() {
        this.geos.forEach(geoObject => {
            geoObject.options.set("visible", false)

        });
        
        console.log('Hide')
    }

    show() {
        this.geos.forEach(geoObject => {
            geoObject.options.set("visible", true)
        });
        console.log('Show')
    }

    prepareMap() {
        for (let index = 0; index < this.coordinates.length; index++) {
            var myPlacemarkWithContent_1 = new ymaps.Placemark(
                this.coordinates[index],
                {
                    iconCaption: index,
                    // –¢–µ–∫—Å—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ –Ω–∞ –º–µ—Ç–∫—É
                    hintContent: "–î–æ–º –ú–∞—Ä–∏–Ω—ã –∏ –ñ–µ–Ω–∏",
                    //–¢–µ–∫—Å—Ç –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –º–µ—Ç–∫—É
                    balloonContent: this.coordinates[index][0] + "<br>" + this.coordinates[index][1],
                },
                {
                    preset: "islands#greenDotIconWithCaption",
                    iconColor: this.polygonColor
                }
            );

            this.myMap.geoObjects.add(myPlacemarkWithContent_1);
            this.geos.push(myPlacemarkWithContent_1);
        }

        // –°–æ–∑–¥–∞–µ–º –º–Ω–æ–≥–æ—É–≥–æ–ª—å–Ω–∏–∫, –∏—Å–ø–æ–ª—å–∑—É—è –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω—ã–π –∫–ª–∞—Å—Å Polygon.
        this.polygon = new ymaps.Polygon(
            [
                // –£–∫–∞–∑—ã–≤–∞–µ–º –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã –≤–µ—Ä—à–∏–Ω –º–Ω–æ–≥–æ—É–≥–æ–ª—å–Ω–∏–∫–∞.
                // –ö–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã –≤–µ—Ä—à–∏–Ω –≤–Ω–µ—à–Ω–µ–≥–æ –∫–æ–Ω—Ç—É—Ä–∞.
                this.coordinates,
            ],
            {
                // –û–ø–∏—Å—ã–≤–∞–µ–º —Å–≤–æ–π—Å—Ç–≤–∞ –≥–µ–æ–æ–±—ä–µ–∫—Ç–∞.
                // –°–æ–¥–µ—Ä–∂–∏–º–æ–µ –±–∞–ª—É–Ω–∞.
                hintContent: "–ú–Ω–æ–≥–æ—É–≥–æ–ª—å–Ω–∏–∫",
            },
            {
                // –ó–∞–¥–∞–µ–º –æ–ø—Ü–∏–∏ –≥–µ–æ–æ–±—ä–µ–∫—Ç–∞.
                // –¶–≤–µ—Ç –∑–∞–ª–∏–≤–∫–∏.
                fillColor: this.polygonColor,
                strokeColor: "#FFFFFF",
                // –®–∏—Ä–∏–Ω–∞ –æ–±–≤–æ–¥–∫–∏.
                strokeWidth: 0.5,
            }
        );

        this.myMap.geoObjects.add(this.polygon);
        this.geos.push(this.polygon);
        console.log("–î–ª–∏–Ω–∞ –ø—É—Ç–∏: " + this.polygon.geometry.getBounds());
    }
}
